"use client"

export const dynamic = "force-dynamic";

import { useState,useEffect, useRef } from "react"
import { useRouter } from "next/navigation";


export default function CreateCardPage() {
    // const router = useRouter()

    const [bg, setBg] = useState<Color>({r: 0, g: 0, b: 0, a: 0.1})
    const colorRef = useRef<Color>(bg)
    const frameRef = useRef<number | null>(null)


    useEffect(() => {
        let start: number | null = null
        let from = colorRef.current
        let to = randomColor()
        const duration = 3000 

        const animate = (time: number) => {
            if (!start) start = time
            const t = Math.min((time - start) / duration, 1)
            const next = mixColor(from, to, t)

            colorRef.current = next
            setBg(next)

            if (t < 1) {
                frameRef.current = requestAnimationFrame(animate)
            } else{
                from = to
                to = randomColor()
                start = null
                frameRef.current = requestAnimationFrame(animate)
            }
        }
        frameRef.current = requestAnimationFrame(animate)

        return () => {
            if (frameRef.current) cancelAnimationFrame(frameRef.current)
        }
    },[])

    const bgStyle = `rgba(${Math.floor(bg.r)}, ${Math.floor(bg.g)}, ${Math.floor(bg.b)}, ${bg.a})`

    return (
        <div 
            className={`flex flex-col justify-center items-center h-full`}
            style={{ 
                backgroundColor: bgStyle
            }}
        >

            <div
                className="border w-1/2 h-1/2 rounded-md"
            >

            </div>

        </div>
    )
}